//IE8
// boolean true:IE8
var identify = function() {
    var ua = navigator.userAgent.toLowerCase();
    var version = 8.0;
    var isIE = ua.indexOf("msie") > -1;
    if (isIE) {
        var safariVersion = ua.match(/msie ([\d.]+)/)[1];
        if (safariVersion <= version) {
            return true;
        } else {
            return false;
        }
    } else {
        return false;
    }
};
module.exports = identify;



